//Imports
@use "../base/variables" as v;
@use "../base/mixins" as m;

.main{
    position: absolute;
    z-index: 1000;
    top: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 10rem;
    gap: 3rem;   
}

.counter{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
}

.timer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;


    p{
        padding-top: 1rem;
        text-transform: uppercase;
        letter-spacing: .2rem;
    }
}

//Tiempos
.time--box{
    width: 100%;
    max-width: 8rem;
    height: 8rem;
    position: relative;

    @include m.tablet(){
        width: 15rem;
        max-width: unset;
        height: 15rem;
    }

    .box{
        position: absolute;
        width: 100%;
        height: 50%;
        border-radius: 5px;
    }

    .box--superior{
        top: 0;
        background: darken($color: v.$DarkDesaturatedBlue, $amount: 7);
    }

    .box--inferior{
        bottom: 0;
        background: v.$DarkDesaturatedBlue;
        background: darken($color: v.$DarkDesaturatedBlue, $amount: 2);
    }

    .time{
        position: absolute;
        z-index: 500;
        display: flex;
        width: 100%;
        height: 100%;
        align-items: center;
        justify-content: center;

        p{
            padding: 0;
            font-size: 2.8rem;
            color: v.$SoftRed;

            @include m.tablet(){
                font-size: 4.5rem;
            }
        }
    }
}