html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.4rem;
  font-family: "Red Hat Text", sans-serif;
  position: relative;
}

img, video, picture {
  display: block;
  max-width: 100%;
  width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

h1, p {
  margin: 0;
  padding: 0;
}

h1 {
  color: hsl(0deg, 0%, 100%);
  text-align: center;
  font-size: 2.2rem;
  letter-spacing: 0.4rem;
  text-transform: uppercase;
  width: 70%;
  line-height: 1.5;
}

p {
  color: hsl(237deg, 18%, 59%);
}

.contenedor {
  width: 95%;
  max-width: 140rem;
  margin: 0 auto;
}

.txt-center {
  text-align: center;
}

.txt-transform {
  text-transform: uppercase;
}

.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0;
}

.reset-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.fijar-body {
  overflow: hidden;
}

.bg--body {
  height: 100vh;
  background-repeat: no-repeat;
  background: hsl(234deg, 17%, 12%);
  background: linear-gradient(180deg, hsl(234deg, 17%, 12%) 0%, hsl(235deg, 16%, 14%) 50%, hsl(236deg, 21%, 26%) 100%);
}

.bg--stars {
  height: 100vh;
  width: 100%;
}
.bg--stars img {
  height: 100%;
  object-fit: cover;
}

.main {
  position: absolute;
  z-index: 1000;
  top: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10rem;
  gap: 3rem;
}

.counter {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
}

.timer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.timer p {
  padding-top: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
}

.time--box {
  width: 100%;
  max-width: 8rem;
  height: 8rem;
  position: relative;
}
@media (min-width: 992px) {
  .time--box {
    width: 15rem;
    max-width: unset;
    height: 15rem;
  }
}
.time--box .box {
  position: absolute;
  width: 100%;
  height: 50%;
  border-radius: 5px;
}
.time--box .box--superior {
  top: 0;
  background: #26283b;
}
.time--box .box--inferior {
  bottom: 0;
  background: hsl(236deg, 21%, 26%);
  background: #30324a;
}
.time--box .time {
  position: absolute;
  z-index: 500;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.time--box .time p {
  padding: 0;
  font-size: 2.8rem;
  color: hsl(345deg, 95%, 68%);
}
@media (min-width: 992px) {
  .time--box .time p {
    font-size: 4.5rem;
  }
}

.bg--hills {
  position: absolute;
  bottom: 0;
  height: 25rem;
  width: 100vw;
}
.bg--hills img {
  height: 100%;
  width: 100vw;
  object-fit: fill;
}

.footer--overlay {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2.5rem;
}
.footer--overlay .sm__icon {
  width: 3rem;
  height: 3rem;
}