//Imports
@use "../base/variables" as v;
@use "../base/mixins" as m;

.bg--hills{
    position: absolute;
    bottom: 0;
    height: 25rem;
    width: 100vw;

    img{
        height: 100%;
        width: 100vw;
        object-fit: fill;
    }
}

.footer--overlay{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2.5rem;
    .sm__icon{
        width: 3rem;
        height: 3rem;
    }
    
}