/*************************************************************************

                                 Mixins
                    Para usarlos se usa @include m.funcion;
        Si tiene argumentos se usa @include m.funcion($color: blue);

*************************************************************************/

@mixin backgroundColor($color: white){
    background-color: $color;
}

@mixin grid($columnas, $separacion) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $separacion;
}

/*************************************************************************

                            Media Queries

                    Para usarlos, en el selector css
                       agregar @include m.tamaño{}

**************************************************************************/

@mixin telefono {
    @media (min-width: 576px){
        @content;
    }
}

@mixin telefono-xl{
    @media (min-width: 768px) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: 992px){
        @content;
    }
}

@mixin laptops{
    @media (min-width: 1200px) {
        @content;
    }
}

@mixin escritorio{
    @media (min-width: 1400px) {
        @content;
    }
}