//Imports
@use "variables" as v;
@use "mixins" as m;

//Globales
html{
    font-size: 62.5%;
    box-sizing: border-box;
}

*,*:before,*:after{
    box-sizing: inherit;
}

body{
    font-size: 1.4rem;
    font-family: v.$fuentePrincipal;
    position: relative;
}

img, video, picture{
    display: block;
    max-width: 100%;
    width: 100%;
    height: auto;
}

a{
    text-decoration: none;
}

h1, p{
    margin: 0;
    padding: 0;
}

h1{
    color: v.$blanco;
    text-align: center;
    font-size: 2.2rem;
    letter-spacing: .4rem; 
    text-transform: uppercase;
    width: 70%;
    line-height: 1.5;
}

p{
    color: v.$GrayishBlue;
}

//Utilidades
.contenedor{
    width: 95%;
    max-width: 140rem;
    margin: 0 auto;
}

.txt-center{
    text-align: center;
}

.txt-transform{
    text-transform: uppercase;
}

.no-margin{
    margin: 0;
}

.no-padding{
    padding: 0;
}

.reset-list{
    padding: 0;
    margin: 0;
    list-style-type: none;
}

.fijar-body{
    overflow: hidden;
}

//Backgrounds
.bg--body{
    height: 100vh;
    background-repeat: no-repeat;
    background: v.$MostlyDarkBlue;
    background: linear-gradient(180deg, v.$MostlyDarkBlue 0%, v.$VeryDarkBlue 50%, v.$DarkDesaturatedBlue 100%); 
}

.bg--stars{
    height: 100vh;
    width: 100%;

    img{
        height: 100%;
        object-fit: cover;
    }
}